.resume-button {
  margin-top:0px;
  display: inline-block;
  color: #333; /* Changed text color to dark grey */
  padding: 1em 3em;
  border-radius: 10px;
  text-decoration: none;
  cursor: pointer;
  border: none;
  font-size: 1.5vw;
  font-weight: bold;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease, background-color 0.3s ease; /* Added background-color transition for a smooth color change on hover */
  background-color: #bdbdbd; /* Changed background color to light grey to contrast with the anthracite background */
}



