.book-quote {
  padding-top:70px;
  position: absolute;
  line-height: 1.5;
  margin: 0 auto;
  font-family: 'Brush Script MT', cursive;
  font-style: italic;
  color: #ccc;
  font-size:2.2vw; 
  height: calc(100% + 100px);
  }

.book-quote .quote-text {
  white-space: pre-wrap;
}
.book-quote .letter {
  position: relative;
  opacity: 0;
  animation: slideDown 14s ease-out forwards;
}

@keyframes slideDown {
  0% {
    opacity: 1;
  }
  
  100% {
    transform: translateY(0);
  }
}

