
.skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2vw;
  width: 80%;
  margin: 0 auto;
  padding: 32px;
}

.skill-item {
  flex: 1 1 0%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  box-sizing: border-box;
  cursor: pointer;
  transition: transform 1s ease;
  height:25vw;
}

 .skill-item:hover {
  transform: scale(1.1);
}


.skill-item img {
  max-width: 100%;
  height: 100%; /* Set a fixed height for all images */
  object-fit: contain;
  margin-bottom: 15px;
}

.skill-item p {
  margin: 0;
  font-size: 1.3vw;
  font-weight: bold;
  text-align: center;
}

.skill-item-left {
  transform: rotateY(-45deg);
  opacity: 0.5;
  animation: blink 2s linear infinite;
}

.skill-item-right {
  transform: rotateY(45deg);
  opacity: 0.5;
  animation: blink 2s linear infinite;
}

@keyframes blink {
  0% { opacity: 0.5; }
  50% { opacity: 0.2; }
  100% { opacity: 0.5; }
}


.skill-text-disabled {
  opacity: 0;
  visibility: hidden;
}

.skill-item-1 {
  order: 3;
}

.skill-item-2 {
  order: 2;
}

.skill-item-3 {
  order: 1;
}

