nav {
  background-color: #333;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  z-index: 1000; /* Ensure the Navbar appears above other content */
}


ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

li {
  margin: 0 2rem;
}


a {
  color: #fff;
  text-decoration: none;
  font-size: 2rem;
  padding: 1.5rem 2rem; /* Add padding to create rectangular clickable area */
  transition: color 0.3s ease;
}

a:hover {
  color: #ccc;
  cursor: pointer;
}

