
 .contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 64px 32px;
  background-color: #1c1c5a;
}

 .contact-container h2 {
  font-size: 2.5rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 32px;
}

/* Adjust the font size and style of the contact information */
 .contact-info {
  display: flex; /* Ensure the contact info is displayed on the same line */
  align-items: center;
  font-size: 3rem; /* Increase font size for better readability */
  color: #fff;
  margin-bottom: 32px;
}

/* Style the links */
 .contact-info a {
  color: #fff;
  text-decoration: none;
  margin-right: 50px; /* Add space between each link */
  margin-left: 50px;
}

/* Add hover effect to the links */
 .contact-info a:hover {
  text-decoration: underline;
}

 .horizontal-line {
  width: 65%;
  max-width: 600px; /* Adjust the maximum width as needed */
  height: 1px;
  background-color: #ccc; /* Adjust the color of the line */
  margin: 20px auto; /* Center the line and provide spacing */
}






/* Style the copyright notice */
.copyright {
  font-size: 1.3rem;
  color: #ccc;
}

