#projects {
  background-color: #262626;
  color: #ccc;
  padding: 60px 0;
  position: relative;
  margin: 0 auto;
  width: 85%;
  margin-bottom:-20vw;
}

#projects .subtitle {
  font-family: "Arial Black", Arial, sans-serif;
  font-size: 3.2vw;
  margin-bottom: 10px;
  color: #ccc;
}

.mosaic-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr); /* Use the same number of rows as columns for equal-sized squares */
  gap: 20px; 
  width: 85%; 
  height: auto; /* Let the height be determined by the content and aspect ratio */
  aspect-ratio: 1 / 1; /* Sets the aspect ratio to 1:1 (square) */
  margin: 0 auto; /* Center the mosaic container */
  color: #ADD8E6;
  margin-top: 4vw;
}



.mosaic-square {
  background-color: #363636; 
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.3s ease; 
  color: #bbb;
  font-size: 1.5vw;
  border-radius: 10%; /* Make the image round */
  transition: transform 0.3s ease;
  height:100%;
}

.mosaic-square:hover {
  transform: scale(1.05); /* Scale up the square on hover */
}

.mosaic-square.expanded {
  width: 81%; 
  height: 50%; 
  position: absolute;
  margin: 0 auto; 
  z-index: 999;
  padding: 4vmin; /* Add padding to create space between text and border */
  background-color: #363636;
  color: #bbb;
  border-radius: 5%; /* Make the image round */
  transform:  scale(1); /* adjust scale value as needed */
  transition: transform 0.5s ease;
}


.square-content{
  color: #bbb;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; /* Align items vertically */
}


.proj-title {
  position: absolute; /* Set position to absolute to position relative to the panel */
  font-size: 2.2vw;
  font-weight: bold;
  top:2cm;
}


.proj-container {
display:flex;
  flex-grow:1;
  width: 100%;
  height:100%;
  justify-content: space-between; /* Create space between title and description */
  margin-top: 3vw; /* Optional: Adjust margin to separate title from content */
}

.proj-desc {
  flex: 0 0 calc(60% - 10px); /* Take up 60% of the container width minus padding */
  max-width: calc(60% - 10px); /* Maximum width */
  padding-right: 1.5vw;
  font-size: 1.5vw;
  line-height: 1.4;
  font-family: Arial, sans-serif;
  text-align: justify;
  padding-bottom: 2vw;
}

.proj-links{
align-self: flex-start; /* Align to the left */
padding-left: 4vw; /* Add padding to the left */
}


.proj-img-container {
  flex: 0 0 calc(40% - 10px);
  max-width: calc(40% - 10px);
  height: 100%;
  padding-right: 1vw;
  align-items: center;
  justify-content: center;
  position: relative;
}

.proj-img {
  max-height: 80%; /* Set max-height to 100% of the container's height */
  width:80%;
  border-radius: 1%;
  object-fit: contain;
  object-position: center center;
  position: relative; /* Position the image absolutely within the container */
}

.proj-img:hover {
  transform: scale(1.7); /* Scale up the square on hover */
  transition: transform 0.2s ease;
  transition-delay: 0.5s;
}




