#skills {
  background-color: #262626;
  color: #ccc;
  padding: 60px 0;
  position: relative;
  margin: 0 auto;
  width: 85%;
}

 .subtitle {
  font-family: "Arial Black", Arial, sans-serif;
  font-size: 3.2vw;
  margin-bottom: 10px;
  color: #ccc;
}


 .framework-icons {
  margin-top:50px;
  display: flex;
  justify-content: center; /* Center items horizontally */
  gap: 40px; /* Space between items */
}


.icon {
  max-width: 150px; /* Adjust based on the size of your images */
  height: 4vw; /* Adjust based on the size of your images */
  display: inline-flex;
  align-items: center;
  justify-content: center;
  filter: invert(85%) grayscale(100%) opacity(.9);
  margin-top:-10px;
}

.icon img {
  width: 100%;
  height: 100%;

}


.skills-content-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center items horizontally */
}

#Skills .rotating-animation-container {
  margin-top: 20px; /* Adjust margin as needed */
}
