.typing-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 10px; /* Add margins */
  border-radius: 20px; /* Round the corners */
  padding: 60px; /* Add padding to create space between text and border */
}

.typing-editor {
  position: relative;
  font-family: 'Courier New', Courier, monospace;
  font-size: 2.5vw;
  background-color: #1e1e1e;
  color: #fff;
  padding: 60px;
  border-radius: 20px;
  overflow-wrap: break-word;
}


.keyword {
  color: #b10dc9; /* Electric Purple color */
}

.linebreak{
 color: #ff0000 /* Color for line break \n*/
}

.content {
  color: orange; /* Halloween Orange color */
}

.editor-prefix {
  color: #fff; /* Electric Purple color */
  margin-right: 10px;
}

@keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}

.blinking-caret {
  animation: blink 1s infinite;
}
